.about-return {
    height: 560px;
}

.about-return .main {
    height: 100%;
    display: flex;
    align-items: center;
    width: 100%;
}

.about-return .title {
    font-family: 'Syne', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0;
    text-align: left;
    margin-bottom: 24px;
    text-shadow: 1px 1px 5px #ffffff;
}

.about-return .title span {
    color: var(--brand-color);
    font-family: 'Syne', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0;
    text-align: left;
}

.about-return .subtitle {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 32px;
    text-shadow: 1px 1px 5px #ffffff;
}

.about-return .call-to-action-primary {
    background: var(--brand-color);
    border-radius: 8px;
    border: 1px solid transparent;
    color: white;
    padding: 6px 0;
    font-family: 'Inter', serif;
    text-align: center;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    width: 196px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    margin-right: 24px;
}

.about-return .call-to-action-primary:hover {
    border-color: var(--brand-color);
    background-color: white;
    color: var(--font-color)
}

.about-return .call-to-action-secondary {
    background: white;
    border-radius: 8px;
    border: 1px solid var(--font-color);
    color: var(--font-color);
    padding: 6px 0;
    font-family: 'Inter', serif;
    text-align: center;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    width: 196px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.about-return .call-to-action-secondary:hover {
    border-color: var(--brand-color);
    background-color: var(--brand-color);
    color: white;
}

.about-return .graph {
    text-align: end;
}

@media (max-width: 600px) {
    .about-return {
        height: auto;
        margin-bottom: 160px;
    }

    .about-return .graph {
        text-align: center;
        margin-bottom: 48px;
    }

    .about-return .graph img {
        max-width: 100%;
    }

    .about-return .title {
        font-size: 40px;
        line-height: 120%;
    }

    .about-return .title span {
        font-size: 40px;
        line-height: 120%;
    }

    .about-return .subtitle {
        font-size: 16px;
        line-height: 20px;
    }

    .about-return .call-to-action-primary {
        margin-bottom: 12px;
    }
}
