.topbar {
    background-color: white;
    z-index: 2;
    padding: 12px 0;
    height: 74px;
}

.menu-item {
    font-family: 'inter', serif;
    font-style: normal;
    font-size: 16px;
    line-height: 128%;
    margin-right: 12px;
    color: var(--font-color);
    text-shadow: 1px 1px 2px 2px var(--brand-color);
    transition: 0.2s ease-in-out;
}

.menu-item:hover {
    color: var(--brand-color);
}

.dashboard-button {
    padding: 6px 32px;
    border-radius: 8px;
    border: 1px solid var(--font-color);
    cursor: pointer;
    font-family: 'inter', serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0;
    text-align: center;
    transition: 0.2s ease-in-out;
    color: var(--font-color);
}

.dashboard-button:hover {
    color: white;
    background-color: var(--brand-color);
    border-color: var(--brand-color);
    box-shadow: 1px 1px 5px 2px #00000010;
}

.sidebar {
    display: none;
    width: 100vw;
    height: 100vh;
    background: var(--brand-color);
    position: absolute;
    top: 0;
    left: 100vw;
    padding: 16px;
    transition: 0.5s ease-in-out;
}

.cross {
    margin-right: 16px;
    text-align: right;
    padding: 12px 0;
}

.mo-menu-items {
    margin-top: 80px;
    margin-bottom: 64px;
}

.mo-menu-items div {
    margin-bottom: 24px;
}

.mo-menu-item {
    font-family: 'Syne', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    color: #FFFFFF;
}

.mo-dashboard-button {
    text-align: center;
    padding: 16px 0;
    border: 1px solid white;
    color: white;
    border-radius: 8px;
}

.menu {
    display: none;
}

@media (max-width: 600px) {
    .hide-on-phone {
        display: none !important;
    }

    .menu {
        display: block;
        margin-right: 16px;
    }

    .sidebar {
        display: block;
    }
}


