.faq {
    margin-bottom: 120px;
}

.faq .title {
    font-family: 'Syne', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0;
    text-align: left;
    margin-bottom: 24px;
    text-shadow: 1px 1px 5px #ffffff;
}

.faq .title span {
    color: var(--brand-color);
    font-family: 'Syne', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0;
    text-align: left;
}

.faq .question {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
}

.faq .answer {
    font-family: 'Inter', serif;
    font-style: normal;
    font-size: 16px;
    line-height: 22px;
}

@media (max-width: 600px) {
    .faq .title {
        font-size: 40px;
        line-height: 120%;
    }

    .faq .title span {
        font-size: 40px;
        line-height: 120%;
    }

    .faq .subtitle {
        font-size: 16px;
        line-height: 20px;
    }
}
