.hero {
    height: calc(100vh - 74px);
}

.hero .pitch {
    height: 100%;
    display: flex;
    align-items: center;
}

.hero .title {
    font-family: 'Syne', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0;
    text-align: left;
    margin-bottom: 24px;
    text-shadow: 1px 1px 5px #ffffff;
}

.hero .title span {
    color: var(--brand-color);
    font-family: 'Syne', serif;
    font-size: 48px;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0;
    text-align: left;
}

.hero .subtitle {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 32px;
    text-shadow: 1px 1px 5px #ffffff;
}

.hero .call-to-action {
    background: var(--brand-color);
    border-radius: 8px;
    border: 1px solid transparent;
    color: white;
    padding: 6px 0;
    font-family: 'Inter', serif;
    text-align: center;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    width: 150px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.hero .call-to-action:hover {
    border-color: var(--brand-color);
    background-color: white;
    color: var(--font-color)
}

@media (max-width: 600px) {
    .hero .title {
        font-size: 40px;
        line-height: 120%;
    }

    .hero .title span {
        font-size: 40px;
        line-height: 120%;
    }

    .hero .subtitle {
        font-size: 16px;
        line-height: 20px;
    }
}
