.numbers {
    display: flex;
    justify-content: center;
    height: 214px;
}

.numbers .font {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 700;
    font-size: 60px;
    line-height: 140%;
    text-align: center;
    color: var(--brand-color);
    margin-bottom: 24px;
    text-shadow: 0 0 25px white;
}

.numbers .subtitle {
    font-family: 'Inter', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 128%;
    color: var(--font-color);
    text-shadow: 0 0 25px white;
}

@media(max-width: 600px) {
    .numbers .font {
        font-size: 32px;
        line-height: 140%;
    }
    .numbers .subtitle {
        font-size: 16px;
        line-height: 20px;
    }
}
